    z-index: 30;
}

.arrow-container {
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 0;
    display: inline-block;
    position: absolute;
    top: 8vw;
    cursor: pointer;
    border: 1px solid #535d63;
    z-index: 4;
    border-radius: 5px;
}

.arrow-container:hover {
    background: #535d63;
}

.arrow-container:hover .arrow-left {
    border-right: 10px solid white;
}

.arrow-container:hover .arrow-right {
    border-left: 10px solid white;
}

.right {
    right: -30vw;
}

.left {
    left: -30vw;
}

